@import './styles/mixins.scss';

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Roboto', sans-serif;
}

.App {
  display: grid;

  @include onTablet {
    grid-template-columns: 2fr 1.5fr;
    column-gap: 20px;
  }
}

.container-info-wrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 100px;
}
